* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  font-size: 80%;
}

body {
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', sans-serif;
  line-height: 1.6;
  color: #333;
}

.App {
  min-height: 100vh;
  background-color: #fff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}